/* ==========
FOOTER
========== */

.app-footer {
    border-top: 1px solid #ddd;
    @include flex-column;
    margin: 8rem;
}

.footer-nav-list {
    @include flex-row;
    justify-content: center;
    padding-top: 4rem;

    li {
        padding: 0;
        margin: 0 1.6rem;
    }

    a {
        padding: .8rem .4rem;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        border-bottom-color: transparent;

        &:hover {
            border-bottom-color: $cyan;
        }

        &.selected {
            border-bottom-color: $turq;
        }
    }
}

.footer-contacts {
    @include flex-row;
    justify-content: center;
    padding-top: 4rem;

    li {
        margin: .4rem 2.4rem;
        line-height: 1;

        a {
            display: block;
            line-height: 1;
        }
    }

    .fa-envelope-square,
    .fa-mobile-alt,
    .fa-linkedin {
        fill: $cyan;
        margin: 0 .4rem -.2rem 0;
        height: 1.6rem;
    }
}

@media screen and (max-width: 1400px) {

    .app-footer {
        margin-right: 4rem;
        margin-left: 4rem;
    }

}

@media screen and (max-width: 680px) {

    .app-footer {
        margin-right: 1.6rem;
        margin-left: 1.6rem;
    }

    .footer-nav-list {
        display: none;
    }

    .footer-contacts {
        @include flex-column;
        justify-content: center;
        align-items: center;
        padding-top: 0;

        li {
            margin-top: 2.4rem;
            font-size: 1.8rem;
        }

        .fa-envelope-square,
        .fa-mobile-alt,
        .fa-linkedin {
            margin: 0 .4rem 0 0;
        }
    }

}