/* ==========
General holding page for case studies
========== */

$color: #121428;
$bar: #2ec630;

.error-page {
    overflow: hidden;

    @include flex-column;
    justify-content: center;
    align-items: center;

    width: calc(100vw - 16rem);
    height: calc(100vh - 10rem);
    margin: 0 8rem 4rem 8rem;
    padding: 0;
    background: $color url(../img/background01b.png) no-repeat center center;
    background-size: 100vw 100vh;
    position: relative;

    .content {
        color: #fff;
        width: 100%;
        max-width: 80rem;
        padding: 8rem;
        position: relative;
        background-color: $color;
    }

    .title {
        @include folio-card-title;

        &:before {
            background-color: $bar;
        }
    }

    .link-inline {
        color: #fff;
    }

}

@media screen and (max-width: 1400px) {

    .error-page {
        width: calc(100vw - 8rem);
        margin: 0 4rem 4rem 4rem;
    }
}


@media screen and (max-width: 680px) {

    .error-page {
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem 4rem 1.6rem;

        .content {
            padding: 2.4rem;
        }
    }
}